.app_navbar{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.2rem 6rem;
  
}
.app_navbar_logo{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    position: relative;
    z-index: 100;
}
.app_navbar_logo img{
    width: 140px;
    
}
.app_navbar_links{
    
    display: flex;
    justify-content: flex-end;
    align-items: center;
    
    
}
.app_links{
    position: relative;
    list-style: none;
    
}
.app_dropdown_menu
{
    position: absolute;
    top: 85%;
    left: -50%;
    width: 200px;
    height: auto;
    padding-top: 15px;
    background-color: #fff;
    z-index: 15;
    transition: 0.5s;
    display: none;
       
}
.app_dropdown_menu-child{
    position: absolute;
    top:0%;
    left: 50%;
    width: 200px;
    height: auto;   
    z-index: 15;
    transition: 0.5s;
   
}
.dropdown_link{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    position: relative;
    height: 55px;
    transition: 0.5s;
}

.dropdown_link .dropdown_link-child{
    position: relative;
    top: 0%;
    left: 40%;
    height: 55px;     
    background-color: #fff;
    z-index: 15;
    transition: 0.5s;
    display: none;

}
.dropdown_link:hover .dropdown_link-child{
    display: flex;
    
}
.dropdown_link .dropdown_link-child a{
    color: rgba(0,0,0,.7);
    font-weight: 500;
    padding: 15px 10px ;
}
.app_dropdown_menu li{  
    border-bottom: 1px solid #eee;   
}
.app_dropdown_menu a{
    padding: 15px 10px ;
    color: rgba(0,0,0,.7);
    font-weight: 500;
}
.app_dropdown_menu a:hover{ 
    color: rgba(0,0,0,1);  
}

.sub_menue-icons{    
    font-size: 15px;
    color: #000;
    height: 100%;

   
}
.app_navbar_links li{
    list-style: none;
    margin: 0 1rem;
    cursor: pointer;
}
.app_navbar_links li:not(:last-child):hover{
  color: #3889bc;
}

.app_navbar_small{
  display: none;
}
.app_navbar_small_overlay{
    width: 100%;
    min-height: 100vh;
    height: auto;
    position: absolute;
    top: 0;
    left: 0;
    background: #fff;
    transition: 0.5s ease;
    flex-direction: column;
    z-index: 10;

}

.app_navbar_small_overlay .overlay_close{
    font-size: 27px;
    position: absolute;
    top: 20px;
    right: 20px;
    color: #000;
    cursor: pointer;
    z-index: 20;
}
.app_navbarsmall_links{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: inherit;
  position: relative;
  
  padding: 8rem 1rem;
}
.app_navbarsmall_links li{
    list-style: none;
    margin: 0.5rem;
    font-size: 27px;
    font-family: var(--font-base);
    
}

.app_navbarsmall_links .app_links .app_dropdown_menu .dropdown_link {
    margin: 0px;
    font-size: 18px;
    left: 0;
    
}
.app_navbarsmall_links .app_links .app_dropdown_menu .dropdown_link{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    height: auto;
    padding: 10px;  
    border-bottom: 0;
}

.app_navbarsmall_links li:hover{
    color: #3889bc;
}

.social_icons{
    font-size: 20px;
    margin-right: 2rem;
 
}
.app_navbarsmall_links .app_dropdown_menu{
      position: relative;
      top: 0;
      left: 5%;
      cursor: pointer;
      width: 250px;
}
.app_links:hover .app_dropdown_menu {
    display: block;
}

@media(max-width: 992px){
    .app_navbar{
        padding: 1.5rem;
    }
    .app_navbar_links {
        display: none;
    }
    .app_navbar_small{
        display: flex;
        
    }
   
}
@media(max-width: 650px){
   
    .app_navbar_logo img{
        width: 100px;
    }
}